import { useState } from "react";

const Home = () => {
    const [intro] = useState(
        `I am a fullstack web developer  with a lot to learn but eager to become a part of a bigger team. I enjoy having the opportunity to keep learnign and being
        able to apply it to creating something from nothing. `
    );

    return (
        <div>
            <h1>Hello my name is </h1>
            <h2> Jesus Diaz </h2>
            <br />
            <div className='aboutMe'>

                <h1 className="text"> About Me</h1>
                <br />
                <p>{intro}</p>
            </div>
        </div>
    );
}

export default Home;