const Projects = ({ projects, title }) => {


    // console.log(projects);

    return (
        <div>
            <h2>{title}</h2>
            {projects.map((project) => (
                <div className="card border-primary mb-3" key={project.id}>
                    <h3 className="card-header">{project.name}</h3>
                    <img src={project.img} alt={project.namr} />

                    <div className="card-body primary">
                        <p className="card-text">{project.description}</p>
                    </div>
                    <div className="card-body primary">
                        <a href={project.link} className="card-link" target="_blank" rel="noopener noreferrer">Link</a>
                        <a href={project.repo} className="card-link" target="_blank" rel="noopener noreferrer">Repository</a>
                    </div>
                </div>
            ))
            }
        </div >
    );
}

export default Projects;